<template lang="html">
  <div class="gallery">
    <ul>
      <li v-for="g in gs">
        <img :src="g.src" :alt="g.alt">
        <p>{{g.text}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '../api/api'

export default {
  name: 'gallery',
  props: {
    gs: {
      default: function(){
        return api.index.gallery
      }
    }
  }
}
</script>

<style lang="css" scoped>
.gallery{
  /*width: 100%;*/

}
  ul{
    white-space: nowrap;
    overflow-x: auto;
    padding: 20px 0;
  }
  li{
    width: 180px;
    /*height: 180px;*/
    display: inline-block;
    vertical-align: top;
    border: 0px solid #000;
    margin-left: 15px;
  }
  li img{
    width: 100%;
    background-color: #fff;
  }
  li p {
    padding: 8px  0px 20px;
    white-space: normal;
  }
</style>



