 window.onload = function view(){

   var R = Raphael("map", 600, 300);
   paintMap(R)
   var textAttr = {
     "fill": "#333",
     "font-size": "8px",
     "font-weight": "300",
     "cursor": "pointer"
   }
   for (var state in nm) {
     if (nm.hasOwnProperty(state)) {
       nm[state]['path'].color = Raphael.getColor(1);

       (function(st, state){
         var xx = st.getBBox().x + (st.getBBox().width / 2);
         var yy = st.getBBox().y + (st.getBBox().height / 2);
        // 文字校准

        switch (nm[state]['name']) {
          case '呼伦贝尔':
            xx += 5
            yy += 10
            break;
          case '兴安盟':
            xx -= 8
            yy -= 4
            break;
          case '通辽':
            yy += 2
            break;
          default:
        }

         nm[state]['text'] = R.text(xx, yy, nm[state]['name']).attr(textAttr);

         st[0].onmouseover = function () {
                   st.animate({fill: st.color, stroke: "#eee"}, 500);
           nm[state]['text'].toFront();
                   // R.safari();
               };
               st[0].onmouseout = function () {
                   st.animate({fill: st.color, stroke: "#eee"}, 500);
           nm[state]['text'].toFront();
                   // R.safari();
               };

       })(nm[state]["path"], state)
     }
   }

 }



