<template>
  <div class="home" style="position: fixed">
    <h1>Welcome to Inner Mongolia</h1>
    <transition name="slide-fade" mode="out-in">
    <h2 v-if="show">{{msg}}</h2>

    </transition>
    <router-link to='/index' >
    <div id="logo" @click="go($event, '欢迎游览土尔扈特民俗文化园')">

    </div>
    </router-link>
    <div id="hand" @click="hide($event)">
      <p>点击地图拼合景区</p>
    </div>
    <div id="map">
      <div id="left">
        <router-link to="/dome_house">
        <section class="top" @click="go($event, '欢迎游览 dome house 穹顶屋')">
          <img src="../assets/1.png" alt="迎游览 dome house 穹顶屋">
        </section>
        </router-link>
        <router-link to="/car">
        <section class="mid" @click="go($event, '欢迎游览房车营地')">
          <img src="../assets/3.png" alt="欢迎游览房车营地">
        </section>
        </router-link>
        <router-link to="/visitor">
        <section class="bot" @click="go($event, '欢迎游览游客服务中心')">
          <img src="../assets/5.png" alt="欢迎游览游客服务中心">
        </section>
        </router-link>
      </div>
      <div id="right">
        <router-link to="/tree">
        <section class="top" @click="go($event, '欢迎游览胡杨林')">
          <img src="../assets/2.png" alt="欢迎游览胡杨林">
        </section>
        </router-link>
        <router-link to="/thirteen">
        <section class="left" @click="go($event, '欢迎游览十三家族文化体验区')">
          <img src="../assets/4.png" alt="欢迎游览十三家族文化体验区">
        </section>
        </router-link>
        <router-link to="/leisure">
        <section class="right" @click="go($event, '欢迎游览休闲区和民宿区')">
          <img src="../assets/6.png" alt="欢迎游览休闲区和民宿区">
        </section>
      </router-link>
      </div>

    </div>

    <!-- <img src="../assets/b.jpg" alt=""> -->
    <router-view id="view">
    </router-view>
  </div>
</template>

<script>
import '../plugins/nm.js'



export default {
  name: 'hello',
  data () {
    return {
      msg: '欢迎来到额济纳土尔扈特民俗文化园',
      show: true
    }
  },
  methods: {
    hide(event){
      var tar = event.currentTarget
      tar.style.display = 'none'
    },
    go(event, slogan){
      var root = event.currentTarget
      var view = document.querySelector('#div')
      var hand = document.querySelector('#hand')
      var card = this.$el.querySelector('#view')
      var button = card.querySelector('.group-toggle button')
      var a = this.$store.state.a
      if (a) {
        button.classList.add('open')
        card.classList.add('slide')
      } else {
        button.classList.remove('open')
        card.classList.remove('slide')
      }
      this.$store.commit('toggle')

      hand.style.display = 'none'

      view.scrollTop = 0
      this.show = !this.show
      setTimeout(() => {
        this.show = true
      },300)
      this.msg = slogan
      root.classList.add('no-border')
    }
  }
}

</script>


<style scoped >
.slide-fade-enter-active {
  transition: all .8s ease;
}
.slide-fade-leave-active {
  transition: all 1.8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to{
  transform: rotateX(-30deg);
  opacity: 0.2;
}

.no-border{
  border: 0px  solid #fff !important;
  transition: all 1s ease-in;
}
.home{
  position: fixed;
  width: 100%;
  color: #fff;
  transform-style: preserve-3d;
}
img{
  width: 100%;
  height: 100%;
  transition: all 3s linear;
}
h2{
  letter-spacing: 0.01em;
  transform-origin: 0 0;
  border-top: 2px solid #181e26;
}

h1, h2 {
  font-weight: normal;
  background-color: #222932;
  max-width: 768px;
  margin: 0 auto;
}
#logo{
  width: 50px;
  height: 50px;
  position: absolute;
  right: 5px;
  top: 5px;
  z-index: 5;
  background: url('../../static/logo1.png') no-repeat 50%;
  background-size: 100% auto;
}
#hand{
  width: 60px;
  height: 90px;
  position: absolute;
  top: 100px;
  right: 50px;
  z-index: 5;
  background: url('../../static/hand.svg') no-repeat 0 0;
  background-size: auto 60%;
  animation: 0.8s ease-in infinite alternate tap;
  transform: translateY(0) rotateX(0);
  transform-origin: 0 0;
  background-color: red;
}
#hand p{
  position: absolute;
  bottom: 0;
  padding: 0 5px;
}
@keyframes tap {
  0{
    transform: translateY(0) rotateX(0);
  }
  100%{
    transform: translateY(15px) rotateX(20deg);
  }
}
#map{
  /*background: url('../assets/222.png') no-repeat;*/
  background-size: 100%;
  height: 280px;
  position: relative;
  max-width: 768px;
  margin: 0 auto;
}
#view{
  z-index: 6;
}
#left{
  position: absolute;
  top: 0;
  left: 0;
  width: 26.875%;
  height: 100%;
  z-index: 1;
}
#left section{
  width: 100%;
  border: 2px solid #fff;
  box-sizing: border-box;
  transition: all 1s linear;
}
#left .top{
  height: 26.531%;
}
#left .mid{
  height: 34.6939%;
}
#left .bot{
  height: 38.8306%;
}
#right{
  position: absolute;
  top: 0;
  left: 26.875%;
  width: 73.125%;
  height: 100%;
  z-index: 1;
}
#right section{
  width: 100%;
  border: 2px solid #fff;
}
#right .top{
  height: 26.531%;
}
#right .left{
  width: 50%;
  height: 73.4694%;
  display: inline-block;
  float: left;
}

#right .right{
  width: 50%;
  height: 73.4694%;
  display: inline-block;
  float: left;
}


ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



